<template>
  <div>
    <h1>Parent</h1>
    <ul>
      <li v-for="pe in parents">
        {{pe.name}} - {{pe.relation}}
      </li>
    </ul>
  </div>
</template>

<script>
export default{
  data(){
    return{
      parents:[
        // {name:"张三",relation:"xy"},
        // {name:"李四",relation:"yy"},
      ]
    }
  },
  //http://ctl-springboot-web01.chinacloudsites.cn/parent/all
  //http://jsonplaceholder.typicode.com/users
  created(){
    this.$http.get("http://127.0.0.1/parent/all")
    .then((data)=>{
       //console.log(data.body)
      this.parents = data.body
    })
    //this.$http.jsonp('http://ctl-springboot-web01.chinacloudsites.cn/parent/all', {},{
    // this.$http.jsonp('http://jsonplaceholder.typicode.com/users', {},{
    //   headers: {},
    //   emulateJSON: true }).then((response) => {
    //    //this.movie = response.data;
    //    //console.log(response);
    //  });
    // this.$http.jsonp("http://127.0.0.1/parent/all",{
    //                             params:{
    //                                 word:'a'
    //                             },
    //                             jsonp:"xxcallback"
    //                         }).then(resp=>{
    //                             console.log(resp);
    //                         });
  }
}
</script>

